
















































































































































































































































.page-heading,
.page-contents {
    display: grid;
}
.page-heading {
    grid-template-columns: 3fr 1fr 1fr 1fr;
    grid-template-areas: "heading-caption heading-stat-1 heading-stat-2 heading-stat-3";
    grid-column-gap: 1rem;
}

.heading-caption {
    grid-area: heading-caption;
}
.heading-stat-1 {
    grid-area: heading-stat-1;
}
.heading-stat-2 {
    grid-area: heading-stat-2;
}
.heading-stat-3 {
    grid-area: heading-stat-3;
}
.page-contents {
    grid-template-columns: repeat(3, minmax(240px, 480px));
    grid-template-areas:
        "content-1 content-2 content-3"
        "content-4 content-4 content-5";
    grid-column-gap: 1rem;
    grid-row-gap: 2rem;
}
.content-1 {
    grid-area: content-1;
}
.content-2 {
    grid-area: content-2;
}
.content-3 {
    grid-area: content-3;
}
.content-4 {
    grid-area: content-4;
}
.content-5 {
    grid-area: content-5;
    max-width: 360px;
}
div[class*="content-"] {
    display: flex;
    flex-direction: column;
}
div[class*="content-"] > div {
    min-height: 4em;
    justify-content: left;
    background: rgba(128, 128, 128, 0.35);
    margin: 1px 0;
    width: 100%;
}
div[class*="content-"] > div:nth-child(2) {
    min-height: 3.5em;
    background: rgba(128, 128, 128, 0.55);
}

div[class*="content-"] > div:nth-child(3) {
    min-height: 1rem;
}

.caption-icon {
    font-size: 2.5rem;
    display: inline-flex;
}
.date {
    width: 100%;
    min-width: 240px;
    max-height: 250px;
    background: none;
}

@media (max-width: 1140px) {
    .page-contents {
        grid-template-columns: repeat(2, minmax(240px, 1fr));
        grid-template-areas:
            "content-1 content-1"
            "content-2 content-3"
            "content-4 content-4"
            "content-5 content-5";
    }
    .page-heading {
        grid-template-columns: 2fr repeat(3, 1fr);
    }
}
@media (max-width: 850px) {
    .page-contents {
        grid-template-columns: 100%;
        grid-template-areas:
            "content-1"
            "content-2"
            "content-3"
            "content-4"
            "content-5";
    }
    .page-heading {
        grid-template-columns: repeat(4, 1fr);
    }
}
@media (max-width: 580px) {
    .page-heading {
        grid-template-columns: repeat(3, 1fr);
        grid-template-areas:
            "heading-caption heading-caption heading-caption"
            "heading-stat-1 heading-stat-2 heading-stat-3";
    }
}
@media (max-width: 320px) {
    .caption-icon {
        display: none;
    }
}

@media (min-width: 1840px) {
    .page-heading {
        grid-template-columns: 744px repeat(3, 248px);
        grid-template-areas: "heading-caption heading-stat-1 heading-stat-2 heading-stat-3";
    }
}
